import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { BasicInfoComponent } from './basic-info/basic-info.component';
import { UserListComponent } from './user-list/user-list.component';
import { PersonnelInfoComponent } from './personnel-info/personnel-info.component';
import { AdditionalInfoComponent } from './additional-info/additional-info.component';
import { AllowSystemComponent } from './allow-system/allow-system.component';
import { AllowBankComponent } from './allow-bank/allow-bank.component';
import { AllowOperatingUnitComponent } from './allow-operating-unit/allow-operating-unit.component';
import { AllowSubUnitComponent } from './allow-sub-unit/allow-sub-unit.component';
import { AddBulkUserComponent } from './add-bulk-user/add-bulk-user.component';

import { ACTION } from 'src/app/enums/action.enum';




const routes: Routes = [
  {
    path: "",
    children: [
      {
        path: '',
        redirectTo: 'user',
        pathMatch: 'full',
      },
      {
        path: "user",
        component: UserListComponent,

        data: {
          title: "Add User",
          permission: [
            ACTION.VIEW,
          ],
          urls: [
            { title: "Add User" },
            { title: "List" },
          ],
        },
      },
      {
        path: "user_add",
        component: BasicInfoComponent,

        data: {
          title: "Add User",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Add User"},
            { title: "Add" },
          ]
        }
      },
      {
        path: "bulk_user_add",
        component: AddBulkUserComponent,

        data: {
          title: "Bulk Add User",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Bulk Add User"},
            { title: "Add" },
          ]
        }
      },
      {
        path: "personnal_info",
        component: PersonnelInfoComponent,

        data: {
          title: "Personnal Information",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Personnal Information"},
            { title: "Add" },
          ]
        }
      },
      {
        path: "Additional_info",
        component: AdditionalInfoComponent,

        data: {
          title: "Additional Information",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Additional Information"},
            { title: "Add" },
          ]
        }
      },
      {
        path: "allow_System",
        component: AllowSystemComponent,
        data: {
          title: "Allow System",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Allow System"},
            { title: "Add" },
          ]
        }
      },
      {
        path: "allow_operating_unit",
        component: AllowOperatingUnitComponent,
        data: {
          title: "Allow Operating Unit",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Allow Operating Unit"},
            { title: "Add" },
          ]
        }
      },
      {
        path: "allow_Sub_unit",
        component: AllowSubUnitComponent,
        data: {
          title: "Allow Sub Unit",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Allow Sub Unit"},
            { title: "Add" },
          ]
        }
      },
      {
        path: "allow_Bank",
        component: AllowBankComponent,
        data: {
          title: "Allow Bank",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Allow Bank"},
            { title: "Add" },
          ]
        }
      },
      {
        path: "allow_cash",
        component: AllowBankComponent,
        data: {
          title: "Allow Cash",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Allow Cash"},
            { title: "Add" },
          ]
        }
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AddUserRoutingModule { }
