import { Component, Injector, OnInit ,OnDestroy} from '@angular/core';
import { BaseListClass } from 'src/app/class/base-list-class';
import { ACTION } from 'src/app/enums/action.enum';
import { URLz } from 'src/app/enums/url.enum';
import { MatDialogConfig } from '@angular/material/dialog';
import { UserProfileComponent } from 'src/app/shared/components/dialogs/user-profile/user-profile.component';
@Component({
  selector: 'app-user-list',
  templateUrl: './user-list.component.html',
  styleUrls: ['./user-list.component.css']
})
export class UserListComponent extends BaseListClass implements OnInit ,OnDestroy{

    isSelected : any;
    selected_org;
    seleced_sys;
    additionalPath  = '/user/Additional_info';
    personnalPath = '/user/personnal_info';
    allowSysPath = '/user/allow_System';
    allowOuPath = '/user/allow_operating_unit';
    allowSusPath = '/user/allow_Sub_unit';
    allowAnalysisPath = '/allow_system';
    allowBankPath = '/user/allow_Bank';
    allowCashPath = '/user/allow_cash';
    public __setOrgId : any = '';
    public __setSystemId : string;
    public __userId : string | any;
    org_id;
    sys_id;

  constructor(
    public injector: Injector,
  ) {
    super(injector);
  }
  ngOnInit() {
    this._pathLocation = "/user/user_add";
    this._component = "User List";
    this.__setSystemId = this._fhs._queryParam.sysId;
    this.__userId = parseInt(this._fhs._queryParam.userId);
    this.initTables();
  }
  initTables(){
    this._reset(URLz.$);
    this.initForm();
    this._tbls[URLz.$].columns = ['Action','actions','profile_status','username' ,'name','role','email','mobile', 'city_id', 'ou','su'];
    if(this._ss.checkPermission(ACTION.STATUS)){
      this._tbls[URLz.$].columns.splice(2, 0, 'Status')
    }
    this._tbls[URLz.$].endpoint = URLz.ADD_BASIC_INFO;
    this._formCreator(this._tbls[URLz.$]);
    this._tbls[URLz.$].formFilter.get('organisation_id').valueChanges.subscribe(val => {
      if(this.emptyCheck(val)){
        if(this.__setSystemId !== undefined && this.__setSystemId !== null && this.__setSystemId !== ''){
          this._tbls[URLz.$].formFilter.get('system_id').patchValue(parseInt(this.__setSystemId), {emitEvent: false})
        }
      }
    })


    this._tbls[URLz.$].formFilter.get('su').valueChanges.subscribe(val => {
      if(this.emptyCheck(val)){
        this._tbls[URLz.$].formFilter.get('su').patchValue(val, {emitEvent: false})
        this._refresh(URLz.$)
      }
    })

  }

  initForm(){
    this._tbls[URLz.$].formFilter = this._fs._fb.group({
      organisation_id: ['',this._vs._vals('Organisation')],
      system_id: ['',this._vs._vals('System')],
      bg: ['',this._vs._vals('Business Group')],
      le: ['',this._vs._vals('Legal Entity')],
      ou: ['',this._vs._vals('Operating Unit')],
      su: ['',this._vs._vals('Sub Unit')],
    })

  }


  detailDialog(id: string) {
    const data = id;
    const config: MatDialogConfig = {
      width: '30%',
      data: data
    }
    const dialogRef = this._dialog.open(UserProfileComponent, config);
    dialogRef.afterClosed().subscribe();
  }

  bulkAdd(id: string){
    this._router.navigate(['/user/bulk_user_add', { userId: id}])
  }

  Switch(path,id){
    const form = this._tbls[URLz.$].formFilter
    this.org_id = form.get('organisation_id').value
    this.sys_id = form.get('system_id').value
    if (id) this._router.navigate([path, { userId: id , orgId: this.org_id   , sysId: this.sys_id}]);
  }

  applyClass = { 'col-lg-3': true, 'col-md-3': true};

  ngOnDestroy(): void {
    // this._fhs._queryParam.sysId = '';
    // this._fhs._queryParam.orgId = '';
    // this._fhs._queryParam.userId = '';
  }

}
